    validaCpf() {

        let sForm = this.form;
        let cpf = sForm.controls.itin.value;
        this.isCpfValid = false;
        this.showCpfValidation = true;

        let erro: String = "";

        if (cpf.length === 11) {
            var nonNumbers = /\D/;
            if (nonNumbers.test(cpf)) {
                erro = "A verificacao de CPF suporta apenas nmeros!";
            }
            else {
                if (cpf == "00000000000" ||
                    cpf == "11111111111" ||
                    cpf == "22222222222" ||
                    cpf == "33333333333" ||
                    cpf == "44444444444" ||
                    cpf == "55555555555" ||
                    cpf == "66666666666" ||
                    cpf == "77777777777" ||
                    cpf == "88888888888" ||
                    cpf == "99999999999") {
                    erro = "Nmero de CPF invlido!"
                }

                let a = [];
                let b: number = 0;
                let c = 11;
                let x: number = 0;
                let y: number = 0;

                for (let i = 0; i < 11; i++) {
                    a[i] = cpf.charAt(i);
                    if (i < 9) b += (a[i] * --c);
                }

                if ((x = b % 11) < 2) {
                    a[9] = 0
                } else {
                    a[9] = 11 - x
                }
                b = 0;
                c = 11;

                for (y = 0; y < 10; y++) b += (a[y] * c--);

                if ((x = b % 11) < 2) {
                    a[10] = 0;
                } else {
                    a[10] = 11 - x;
                }

                if ((cpf.charAt(9) != a[9]) || (cpf.charAt(10) != a[10])) {
                    erro = "Nmero de CPF invlido.";
                }
            }
        } else {
            if (cpf.length === 0)
                return false
            else
                erro = "Nmero de CPF invlido.";
        }

        if (erro.length > 0) {
            return false;
        }
        this.isCpfValid = true;
        return true;
    }


